.MenuObject {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}

.MenuObjectContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}

.MenuObjectContainer > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  /* margin-left: 15px; */
}

.MenuObjectContainer > img {
  width: 1px;
}

.MenuObjectContainer > div > img:nth-child(n) {
  padding-right: 10px;
  width: 20px;
}

.MenuObjectContainer > div > span {
  color: white;
  font-family: "Maven Pro", sans-serif;
}
