.FAQ {
  width: 100%;
  background-color: #115c07;
  display: flex;
  justify-content: center;
}

.FAQcontainer {
  max-width: 1200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.FAQHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 30px;
}

.FAQHeader > span {
  font-family: "Maven Pro", sans-serif;
  color: white;
  font-size: 35px;
  margin-left: 30px;
  text-align: center;
}

.FAQcontainer > span {
  font-family: "Maven Pro", sans-serif;
  color: white;
  font-size: 25px;
  text-align: center;
  margin-bottom: 40px;
}
